  font-family: "circe-regular";
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/Circe/Circe-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "circe-extra-bold";
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/Circe/Circe-ExtraBold.ttf") format("truetype");
}

@font-face {
