}

/* ==================================== */
/* ==================================== *
/* ======== NORMALIZE AND RESET ======= */
/*! normalize-scss | MIT/GPLv2 License | bit.ly/normalize-scss */
html {
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

article,
aside,
footer,
header,
nav,
section {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

figcaption,
figure {
  display: block;
}

figure {
  margin: 1em 40px;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

main {
  display: block;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: inherit;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

dfn {
  font-style: italic;
}

mark {
  background-color: #ff0;
  color: #000;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

audio,
video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

img {
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

button,
input,
optgroup,
select,
textarea {
  font-family: sans-serif;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

input {
  overflow: visible;
}

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-cancel-button, [type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  display: table;
  max-width: 100%;
  padding: 0;
  color: inherit;
  white-space: normal;
}

progress {
  display: inline-block;
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

details {
  display: block;
}

summary {
  display: list-item;
}

menu {
  display: block;
}

canvas {
  display: inline-block;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

/* reset */
*, *::after, *::before {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
}

body, html {
  width: 100%;
}

form {
  margin: 0;
  padding: 0;
}

input, input:focus, textarea, select {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important;
  -webkit-focus-ring-color: rgba(255, 255, 255, 0) !important;
  outline: none !important;
}

button,
textarea,
.select select,
input[type="text"],
input[type="email"],
input[type="search"],
input[type="submit"],
input[type="button"],
input[type="reset"],
input[type="number"],
input[type="range"] {
  appearance: none;
  border-radius: 0;
}

select[disabled] {
  cursor: default;
}

select::-ms-expand {
  display: none;
}

input {
  vertical-align: middle;
}

input::-ms-clear {
  display: none;
}

input[type="radio"]:focus {
  -moz-outline-radius: 12px !important;
  -moz-outline-offset: 0 !important;
}

input[type="checkbox"]:focus {
  -moz-outline-offset: -1px !important;
  -moz-outline: 1px solid #000 !important;
}

input[type="radio"],
input[type="checkbox"],
select {
  cursor: pointer;
}

blockquote, q {
  quotes: none;
}

blockquote::before, blockquote::after, q::before, q::after {
  content: none;
}

ins {
  text-decoration: none;
}

del {
  text-decoration: line-through;
}

fieldset, table, ul {
  margin: 0;
  padding: 0;
  border: 0;
}

table {
  width: 100%;
}

ul {
  list-style: none;
}

/* reset end */
/* browserupgrade */
.old-ie {
  overflow: hidden;
}

.old-ie body {
  overflow: hidden;
}

.browserupgrade {
  position: fixed;
  height: 100%;
  width: 100%;
  padding: 0.2em 0;
  margin: 0;
  font-size: 28px;
  line-height: 42px;
  color: #000;
  background: #ccc;
  z-index: 99999;
}

.browserupgrade p {
  position: absolute;
  width: 100%;
  top: 50%;
  margin: -22px 0 0;
  padding: 0;
  text-align: center;
  vertical-align: middle;
}

/* browserupgrade end */
/* no js warning */
.no-js-warning {
  position: fixed;
  width: 100%;
  z-index: 99999;
  border: 2px solid #f00;
  color: #f00;
  background: #fff;
  padding: 5px 20px;
  font-size: 18px;
}

/* no js warning end */
/* ==================================== */
/* ============== LAYOUT ============== */
/* ==================================== */
html {
  font-size: 62.5%;
  overflow-x: hidden;
}

html.css-scroll-fixed:not(.open-only-mob) {
  width: 100% !important;
  height: 100% !important;
  max-width: 100% !important;
  max-height: 100% !important;
  margin: 0 !important;
  padding: 0 !important;
  overflow: hidden !important;
}

@media (max-width: 991px) {
  html.css-scroll-fixed.open-only-mob {
    width: 100% !important;
    height: 100% !important;
    max-width: 100% !important;
    max-height: 100% !important;
    margin: 0 !important;
    padding: 0 !important;
    overflow: hidden !important;
  }
}

body {
  min-width: 32rem;
  font-family: "avenir-next-cyr-medium", Helvetica, Arial, sans-serif;
  font-size: 1.8rem;
  line-height: 2.6rem;
  color: #3f3f3f;
  overflow-x: hidden;
  background: #fff url(../img/empty.png) no-repeat left top fixed;
  transition: color 0.7s, background-color 0.7s;
}

@media (max-width: 639px) {
  body {
    font-size: 1.6rem;
    line-height: 2.4rem;
  }
}

.layout-page-spaces {
  padding-left: 6.5rem;
  padding-right: 6.5rem;
}

.layout-page {
  box-sizing: content-box;
  max-width: 192rem;
  margin-left: auto;
  margin-right: auto;
}

.layout-text-spaces {
  padding-left: 6.5rem;
  padding-right: 6.5rem;
}

.layout-text {
  box-sizing: content-box;
  max-width: 92rem;
  margin-left: auto;
  margin-right: auto;
}

.layout-flood {
  box-sizing: content-box;
  max-width: 110rem;
  margin-left: auto;
  margin-right: auto;
}

.layout-form {
  box-sizing: content-box;
  max-width: 48rem;
}

body {
  display: flex;
  flex-direction: column;
}

.header {
  flex-shrink: 0;
}

.main {
  flex: 1 0;
}

.footer {
  flex-shrink: 0;
  margin-top: auto;
}

/* ==================================== */
/* ============ TYPOGRAPHY ============ */
/* ==================================== */
/* ==================================== */
/* ======= NON CONTENT ELEMENTS ======= */
/* ==================================== */
/* ==================================== */
/* ============ COMPONENTS ============ */
/* ==================================== */
/* ==================================== */
/* ============== HEADER ============== */
/* ==================================== */
.header {
  position: relative;
  z-index: 50;
}

.header_fixed {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  min-width: 32rem;
}

.header-container {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 2rem 0 0 0;
  line-height: 1;
}

@media (max-width: 991px) {
  .header-container {
    padding-top: 5rem;
    padding-right: 4.9rem;
  }
}

@media (max-width: 639px) {
  .header-container {
    padding-top: 8.571rem;
    padding-right: 4.9rem;
  }
}

.header-container__left {
  position: relative;
}

.header-container__right {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 991px) {
  .header-container__right {
    display: none;
  }
}

/* logo */
.logo {
  overflow: hidden;
  position: relative;
  margin: 0.7rem 0 0;
  width: 23.8rem;
  flex-shrink: 0;
  z-index: 5;
}

@media (max-width: 991px) {
  .logo {
    width: 38.8rem;
  }
}

@media (max-width: 639px) {
  .logo {
    width: 63.506rem;
    height: 19.48rem;
  }
}

.logo::before {
  content: "";
  display: block;
  padding-top: 25.21%;
}

.logo a,
.logo > span {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: 5;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}

.logo a > span,
.logo > span > span {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.logo a > span:active, .logo a > span:focus,
.logo > span > span:active,
.logo > span > span:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}

.logo .logo_dark img,
.logo .logo_light img {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  /* width: 100%; */
  pointer-events: none;
  background-repeat: no-repeat;
  background-size: contain;
  will-change: opacity;
  transition: opacity 0.8s;
}

.logo .logo_light {
  opacity: 0;
}

.logo-theme-light .logo .logo_dark {
  opacity: 0;
}

.logo-theme-light .logo .logo_light {
  opacity: 1;
}

.header-container__right .jsNav__item {
  background: transparent;
  border-radius: 6px;
}

.logo-theme-dark .header-container__right .jsNav__item {
  background: #fff;
  border-radius: 6px;
  transition: background 0.8s;
}

/* logo end */
/* lang */
.lang {
  font-family: "circe-extra-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
  color: #3f3f3f;
  font-size: 1.4rem;
  line-height: 2.6rem;
  text-align: center;
  text-transform: uppercase;
}

@media (max-width: 991px) {
  .lang {
    font-size: 2.7rem;
  }
}

@media (max-width: 639px) {
  .lang {
    font-size: 5.455rem;
    line-height: 8.182rem;
  }
}

.lang__inner {
  height: 2.6rem;
  width: 2.6rem;
  position: relative;
  display: inline-block;
  vertical-align: top;
  text-decoration: none;
  color: inherit;
  transition: color 0.35s;
  z-index: 1;
}

.lang__inner::before {
  content: "";
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  height: 4rem;
  width: 4rem;
  border-radius: 4rem;
  margin: -2.1rem 0 0 -2rem;
  opacity: 0;
  background-color: black;
  transition: opacity 0.35s, transform 0.35s, background-color 0.35s;
  transform: translateZ(0) scale3d(0.5, 0.5, 1);
  z-index: -1;
}

@media (max-width: 991px) {
  .lang__inner::before {
    height: 6rem;
    width: 6rem;
    border-radius: 6rem;
    margin: -3.2rem 0 0 -2.4rem;
  }
}

@media (max-width: 639px) {
  .lang__inner::before {
    content: "";
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    height: 11rem;
    width: 11rem;
    border-radius: 10rem;
    margin: -3.2rem 0 0 -3rem;
  }
}

.lang__inner:hover::before {
  opacity: 0.35;
  transform: translateZ(0) scale3d(1, 1, 1);
}

.logo-theme-light .lang__inner {
  color: white;
}

.logo-theme-light .lang__inner::before {
  background-color: white;
}

/* lang end */
.play_button {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 10;
  opacity: 1;
  background: url(../img/play_video_on_slider.svg) no-repeat center center;
  outline: none;
  border: none;
}

.play_button.o {
  opacity: 0;
}

/* ==================================== */
/* ============ NAVIGATION ============ */
/* ==================================== */
.nav-wrap {
  position: relative;
  flex: 1 0 auto;
  text-align: center;
  z-index: 50;
}

@media (max-width: 991px) {
  .nav-wrap {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 48rem;
    max-width: 100%;
    text-align: left;
    background-color: rgba(238, 110, 115, 0.9);
    z-index: 999;
    transition-property: opacity, visibility, transform;
    transition-duration: 0.2s;
    opacity: 0;
    transform: translate(-100%, 0);
    transform: translate3d(-100%, 0, 0);
  }
}

@media (max-width: 767px) {
  .nav-wrap {
    width: 32rem;
  }
}

@media (max-width: 639px) {
  .nav-wrap {
    width: 100%;
  }
}

@media (max-width: 991px) {
  .nav-wrap.is-open {
    opacity: 1;
    transform: translate(0, 0);
    transform: translate3d(0, 0, 0);
  }
}

.nav-overlay {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 998;
  transition: opacity 0.2s;
  opacity: 0;
  pointer-events: none;
  display: none;
}

@media (max-width: 991px) {
  .nav-overlay {
    display: block;
  }
}

.nav-wrap.is-open ~ .nav-overlay {
  opacity: 1;
  pointer-events: auto;
}

.nav-opener {
  display: block;
  position: relative;
  height: 4rem;
  width: 4rem;
  font-size: 0;
  line-height: 0;
  cursor: pointer;
  display: none;
}

.nav-opener::before, .nav-opener::after {
  content: "";
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-repeat: no-repeat;
  background-position: 50%;
}

.nav-opener::before {
  height: 1.7rem;
  width: 2.6rem;
  background-image: url(data:image/svg+xml;base64,);
}

.nav-opener::after {
  height: 2.5rem;
  width: 2.7rem;
  background-image: url(data:image/svg+xml;base64,);
  display: none;
}

@media (max-width: 991px) {
  .nav-opener {
    display: block;
  }
}

.nav-opener.is-open::before {
  display: none;
}

.nav-opener.is-open::after {
  display: block;
}

.nav-header {
  position: relative;
  align-items: center;
  justify-content: space-between;
  border-bottom: 0.1rem solid rgba(255, 255, 255, 0.5);
  display: none;
}

@media (max-width: 991px) {
  .nav-header {
    display: flex;
  }
}

.logo-nav {
  display: block;
  position: relative;
  height: 4rem;
  width: 16rem;
  font-size: 0;
  line-height: 0;
  background-image: url(../img/logo-alt.svg);
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: 85%;
}

.nav-close-btn {
  position: relative;
  display: block;
  height: 4rem;
  width: 4rem;
  margin-left: auto;
  overflow: hidden;
  text-indent: -999rem;
}

.nav-close-btn::before {
  content: "";
  display: block;
  height: 1.8rem;
  width: 1.8rem;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-repeat: no-repeat;
  background-position: 50%;
  background-image: url(data:image/svg+xml;base64,);
  background-size: contain;
}

.nav {
  position: relative;
  display: inline-block;
  vertical-align: top;
}

@media (max-width: 991px) {
  .nav {
    display: block;
    padding: 0;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
  }
}

.nav__list {
  display: flex;
  flex-wrap: wrap;
  cursor: default;
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  font-weight: 400;
  font-size: 1.2rem;
  line-height: 1.6rem;
  color: #3f3f3f;
  text-align: left;
  transition: color 0.7s;
}

.logo-theme-light .nav__list {
  color: #fff;
}

@media (max-width: 1199px) {
  .nav__list {
    font-size: 1.2rem;
    line-height: 1.8rem;
  }
}

@media (max-width: 991px) {
  .nav__list {
    font-size: 1.4rem;
    line-height: 1.8rem;
  }
}

.nav__list,
.nav__list ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

@media (max-width: 991px) {
  .nav__list {
    display: block;
  }
}

.nav__list li {
  position: relative;
}

@media (max-width: 991px) {
  .nav__list li {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
  }
}

.nav__list a,
.nav__list li > span {
  display: inline-block;
  vertical-align: top;
  position: relative;
  overflow: hidden;
  padding: 0.5rem;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}

@media (max-width: 991px) {
  .nav__list a,
  .nav__list li > span {
    padding: 0 1rem 0 0;
    overflow: visible;
  }
}

.nav__list a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

@media (max-width: 991px) {
  .nav__list a {
    max-width: calc(100% - 4rem);
  }
}

.nav__list li.has-sub > a,
.nav__list li.has-sub > span {
  padding-right: 4rem;
}

@media (max-width: 991px) {
  .nav__list li.has-sub > a,
  .nav__list li.has-sub > span {
    padding-right: 1rem;
    max-width: calc(100% - 4rem);
  }
}

.nav__list ul {
  position: relative;
  flex-shrink: 0;
  width: 100%;
  flex-basis: 100%;
  display: none;
  transition: opacity 0.2s, visibility 0.2s;
}

@media (min-width: 992px) {
  .nav__list ul {
    display: block !important;
    position: absolute;
    right: 0;
    top: 100%;
    width: 28rem;
    padding: 1.5rem 0;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }
}

.nav__list ul::before {
  content: "";
  display: block;
  position: absolute;
  top: 1rem;
  left: -0.5rem;
  height: calc(100% - 1rem * 2);
  width: calc(100% - -0.5rem * 2);
  background-color: #e8e8e8;
  border-radius: 0.2rem;
  z-index: -1;
}

@media (max-width: 991px) {
  .nav__list ul::before {
    content: none;
  }
}

.nav__list ul ul {
  left: 100%;
  top: -0.5rem;
  text-transform: none;
}

@media (max-width: 991px) {
  .nav__list ul ul {
    left: auto;
    top: auto;
